import React, { useState } from "react";
import "./JoinUs.css";
import NavBar from "./NavBar.js";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "animate.css/animate.min.css";
import ScrollAnimation from "react-animate-on-scroll";
import TextField from "@material-ui/core/TextField";
import { makeStyles } from "@material-ui/core/styles";
import "react-phone-number-input/style.css";
import PhoneInput from "react-phone-number-input";
import Button from "@material-ui/core/Button";
import "./Contact.css";
import Popup from "./Popup.js";
import Popup2 from "./Popup2.js";
import Popup3 from "./Popup3.js";
import Image from "react-bootstrap/Image";
const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "25ch",
    },
  },
}));

function JoinUs() {
  const [value, setValue] = useState();
  const classes = useStyles();
  return (
    <div className="join-us">
      <NavBar />
      <Row className="join-us-mainRow">
        <Col md={12} xs={12} className="join-us-page-title">
          <h1 className="join-our-team-h1-title">
            Join Our <span className="join-our-team-span">Team</span>
          </h1>
        </Col>
        <Col md={6} xs={6} className="job-on-board-card">
          <Row>
            <Col md={5}>
              <h1 className="job-on-board-card-h1-title">Jobs on Board</h1>
            </Col>
            <Col md={7} className="job-on-board-card-para-content">
              <p>
                Apply to our open positions below or contact us at{" "}
                <span className="itpeaks-email-span">careers@itpeaks.com</span>{" "}
                <br />
                to learn more about employment opportunities.
              </p>
            </Col>
          </Row>
        </Col>
        <Col md={6} xs={6} className="job-vacancies-card">
          <Row>
            <Col md={4} xs={12} className="job-vacancies-leftSide-col">
              <Image
                src="https://i.ibb.co/StC0rng/CRM-developer-job-Card.jpg"
                alt=""
                className="job-vacancies-leftSide-images"
              />
            </Col>
            <Col md={8} xs={12} className="job-vacancies-rightSide-col">
              <div className="job-vacancies-rightSide">
                <h4 className="thirdRow4-cols2-h4">Sr. CRM Developer</h4>
                <h6 className="thirdRow4-cols2-h6">
                  Aswan, Egypt • Full-time •
                </h6>
                <p>
                  Lorem, ipsum dolor sit amet consectetur adipisicing elit. Ea
                  officiis ducimus autem doloremque totam nihil reprehenderit in
                  tempore commodi ab.
                </p>
                <div className="button-div">
                  <Popup />
                  <a href="#apply" className="apply-button">
                    Apply
                  </a>
                </div>
              </div>
            </Col>
          </Row>
        </Col>
        <Col md={6} xs={6} className="job-vacancies-card">
          <Row>
            <Col md={4} xs={12} className="job-vacancies-leftSide-col">
              <Image
                src="https://i.ibb.co/FqpqtVb/frontend-Developer-jobcard.jpg"
                alt=""
                className="job-vacancies-leftSide-images"
              />
            </Col>
            <Col md={8} xs={12} className="job-vacancies-rightSide-col">
              <div className="job-vacancies-rightSide">
                <h4 className="thirdRow4-cols2-h4">Frontend Developer</h4>
                <h6 className="thirdRow4-cols2-h6">
                  Aswan, Egypt • Full-time •
                </h6>
                <p>
                  Lorem, ipsum dolor sit amet consectetur adipisicing elit. Ea
                  officiis ducimus autem doloremque totam nihil reprehenderit in
                  tempore commodi ab.
                </p>
                <div className="button-div">
                  <Popup2 />
                  <a href="#apply" className="apply-button">
                    Apply
                  </a>
                </div>
              </div>
            </Col>
          </Row>
        </Col>
        <Col md={6} xs={6} className="job-vacancies-card">
          <Row>
            <Col md={4} xs={12} className="job-vacancies-leftSide-col">
              <Image
                src="https://i.ibb.co/DPXNnSY/share-Point-developer-job-Card.jpg"
                className="job-vacancies-leftSide-images"
                alt=""
              />
            </Col>
            <Col md={8} xs={12} className="job-vacancies-rightSide-col">
              <div className="job-vacancies-leftSide">
                <div className="job-vacancy-imageBackground"></div>
              </div>
              <div className="job-vacancies-rightSide">
                <h4 className="thirdRow4-cols2-h4">Sr. SharePoint Developer</h4>
                <h6 className="thirdRow4-cols2-h6">
                  Aswan, Egypt • Full-time •
                </h6>
                <p>
                  Lorem, ipsum dolor sit amet consectetur adipisicing elit. Ea
                  officiis ducimus autem doloremque totam nihil reprehenderit in
                  tempore commodi ab.
                </p>
                <div className="button-div">
                  <Popup3 />
                  <a href="#apply" className="apply-button">
                    Apply
                  </a>
                </div>
              </div>
            </Col>
          </Row>
        </Col>
      </Row>
      <Row className="join-us-main-secondRow2">
        <Col md={1} xs={1} className="join-us-leftSide-inner-col2">
          <h1>
            A<br />P<br />P<br />L<br />L<br />Y<br />
            <br />
            <span className="contact-us-us-word">
              N<br />O<br />W
            </span>
          </h1>
        </Col>
        <Col md={7} xs={12} className="join-us-secondRow2-col">
          <Row className="join-us-secondRow2">
            <h3 className="join-us-secondRow2-title" id="apply">
              Jop Application
            </h3>
            <form
              className={classes.root}
              noValidate
              autoComplete="off"
              className="join-us-secondRow2-form"
            >
              <Row>
                <Col md={6} xs={12} className="secondRow2-cols-1">
                  <ScrollAnimation
                    animateIn="animate__fadeInLeft"
                    animateOut="animate__fadeOut"
                    duration={2}
                  >
                    <TextField
                      id="standard-basic"
                      label="Jop Applying For"
                      type="text"
                      required
                      className="secondRow2-cols-1-input"
                    />
                  </ScrollAnimation>
                </Col>
                <Col
                  md={6}
                  xs={12}
                  className="secondRow2-1-cols join-us-phone-number-input"
                >
                  <ScrollAnimation
                    animateIn="animate__fadeInRight"
                    animateOut="animate__fadeOut"
                    duration={2}
                  >
                    <PhoneInput
                      international
                      defaultCountry="EG"
                      value={value}
                      onChange={setValue}
                      className="secondRow2-cols-1-input"
                      placeholder="Phone number"
                    />
                  </ScrollAnimation>
                </Col>
                <Col md={12} xs={12} className="secondRow2-1-cols">
                  <ScrollAnimation
                    animateIn="animate__fadeInLeft"
                    animateOut="animate__fadeOut"
                    duration={1}
                  >
                    <div className="file-input-div">
                      <input
                        type="file"
                        name="file"
                        id="file"
                        class="inputfile"
                      />
                      <label for="file">Please insert your resume *</label>
                    </div>
                  </ScrollAnimation>
                </Col>
              </Row>
              <Row>
                <Col md={12} xs={12} className="secondRow2-1-cols-textarea">
                  <ScrollAnimation
                    animateIn="animate__fadeIn"
                    animateOut="animate__fadeOut"
                    duration={1}
                  >
                    <TextField
                      id="standard-basic"
                      label="What makes you the perfect candidate?"
                      multiline
                      rows={4}
                      rowsMax={6}
                      required
                      className="secondRow2-cols-input-1"
                    />
                  </ScrollAnimation>
                </Col>
              </Row>
              <Row>
                <Col md={12} xs={12} className="secondRow2-1-cols-button">
                  <ScrollAnimation
                    animateIn="animate__fadeIn"
                    animateOut="animate__fadeOut"
                    duration={1}
                  >
                    <Button variant="contained" className="about-us-fp-button">
                      Submit
                    </Button>
                  </ScrollAnimation>
                </Col>
              </Row>
            </form>
          </Row>
        </Col>
      </Row>
    </div>
  );
}

export default JoinUs;
