import React from "react";
import "./NavBar.css";
import { Link } from "react-router-dom";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";

function NavBar() {
  return (
    <div>
      <div className="navBar">
        <Nav className="navItems">
          <div>
            <Link to="/">
              <Navbar.Brand className="navBarBrand" href="/">
                <img
                  src="https://i.ibb.co/rfBPYsx/itpeaks-Logo2.png"
                  className="navBarBrand-img"
                  width="100"
                  height="50"
                  alt=""
                  href="/"
                />
              </Navbar.Brand>
            </Link>
          </div>
          <Navbar collapseOnSelect>
            <Navbar.Brand className="navBarBrand2" href="/">
              <Link to="/">
                <img
                  src="https://i.ibb.co/RBwNtkX/itpeaks-Logo-Edited.png"
                  width="70"
                  height="70"
                  className="d-inline-block align-top navBarBrand-img"
                  alt=""
                  href="/"
                />
              </Link>
            </Navbar.Brand>
          </Navbar>
          <Navbar
            collapseOnSelect
            expand="xl"
            className="navbar-bootstrapNavbar"
          >
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
              <Nav className="mr-auto nav-container">
                <div className="navbar-items-div">
                  <Nav.Link className="contactUs-Nav nav-link">
                    <Link to="/about" className="aboutLink">
                      About
                    </Link>
                  </Nav.Link>
                  <Nav.Link className="blog-Nav nav-link" to="/services">
                    <Link to="/services" className="servicesLink">
                      Services
                    </Link>
                  </Nav.Link>

                  <Nav.Link className="blog-Nav nav-link">
                    <Link to="/contact" className="contactLink">
                      Blog
                    </Link>
                  </Nav.Link>
                  <Nav.Link className="contactUs-Nav nav-link">
                    <Link to="/contact" className="contactLink">
                      Contact
                    </Link>
                  </Nav.Link>
                </div>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
        </Nav>
      </div>
    </div>
  );
}

export default NavBar;
