import React from "react";
import "./Home.css";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "animate.css/animate.min.css";
import ScrollAnimation from "react-animate-on-scroll";
import "./NavBar.css";
import NavBar from "./NavBar.js";
import "./Services.css";
import Services from "./Services.js";
import AboutMini from "./AboutMini.js";
import peaks from "../img/peak.mp4";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
    },
  },
}));

function Home() {
  return (
    <div className="landing-page">
      <NavBar />
      <div className="overlay"></div>
      <video autoPlay loop muted className="home-video-background">
        <source src={peaks} type="video/mp4" />
      </video>
      <div className="home">
        <div className="home-section">
          <Row className="home-title-row-container">
            <Col className="home-title-col-container row1-cols" md={12}>
              <h1 className="home-title">REACH THE SUMMIT</h1>
            </Col>
            <Col className="home-subtitle-col row1-cols" md={12}>
              <h3 className="home-subtitle">
                ITPEAKS provides solutions for all of your Technology needs We
                support you in any IT endeavor and ensure you cross the finish
                line in record time.{" "}
                <span className="home-subtitle-span">
                  We help you reach the top.
                </span>
              </h3>
            </Col>
            <Col className="home-button  row1-cols">
              <ScrollAnimation
                animateIn="animate__fadeIn"
                animateOut="animate__fadeIn"
                duration={3}
              >
                <Button variant="contained" className="home-button-contact-us">
                  GET started
                </Button>
              </ScrollAnimation>
            </Col>
          </Row>
        </div>
      </div>
      <AboutMini />
      <Services />
    </div>
  );
}

export default Home;
