import React, { useState } from "react";
import "./Contact.css";
import NavBar from "./NavBar.js";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "animate.css/animate.min.css";
import ScrollAnimation from "react-animate-on-scroll";
import TextField from "@material-ui/core/TextField";
import { makeStyles } from "@material-ui/core/styles";
import "react-phone-number-input/style.css";
import PhoneInput from "react-phone-number-input";
import Button from "@material-ui/core/Button";
import Container from "@material-ui/core/Container";
import Image from "react-bootstrap/Image";
const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "25ch",
    },
  },
}));

function Contact() {
  const [value, setValue] = useState();
  const classes = useStyles();
  return (
    <Container maxWidth="xl" disableGutters>
      <div className="contact">
        <NavBar />
        <Row className="contact-us-mainRow">
          <Col md={4} xs={12} className="contact-us-leftSide-inner-col1">
            <Image
              src="https://i.ibb.co/X3BW9WG/say-Hello-Image.jpg"
              roundedCircle
              width="300"
              height="300"
            />
            <h3 className="h3-say-hello">Say hello</h3>
            <p className="contatct-us-leftSide-para">
              Fill in the form below and we’ll be in touch with you shortly.
              Don’t worry, we will never share your information with anyone.
              Fields marked with an * are required
            </p>
          </Col>
          <Col md={1} xs={1} className="contact-us-leftSide-inner-col2">
            <h1>
              C<br />O<br />N<br />T<br />A<br />C<br />T<br />
              <br />
              <span className="contact-us-us-word">
                U<br />S
              </span>
            </h1>
          </Col>
          <Col md={7} xs={12} className="secondRow2-col">
            <Row className="secondRow2">
              <ScrollAnimation
                animateIn="animate__fadeIn"
                animateOut="animate__fadeOut"
                duration={2}
              >
                <h3 className="secondRow2-title">
                  Please fill in the following inputs
                </h3>
              </ScrollAnimation>
              <form
                className={classes.root}
                noValidate
                autoComplete="off"
                className="secondRow2-form"
              >
                <Row>
                  <Col md={4} xs={12} className="secondRow2-cols">
                    <ScrollAnimation
                      animateIn="animate__fadeInLeft"
                      animateOut="animate__fadeOut"
                      duration={2}
                    >
                      <TextField
                        id="standard-basic"
                        label="First Name"
                        type="text"
                        required
                        className="secondRow2-cols-input"
                      />
                    </ScrollAnimation>
                  </Col>
                  <Col md={4} xs={12} className="secondRow2-cols">
                    <ScrollAnimation
                      animateIn="animate__fadeIn"
                      animateOut="animate__fadeOut"
                      duration={2}
                    >
                      <TextField
                        id="standard-basic"
                        label="Last Name"
                        type="text"
                        required
                        className="secondRow2-cols-input"
                      />
                    </ScrollAnimation>
                  </Col>

                  <Col md={4} xs={12} className="secondRow2-cols">
                    <ScrollAnimation
                      animateIn="animate__fadeInRight"
                      animateOut="animate__fadeOut"
                      duration={2}
                    >
                      <TextField
                        id="standard-basic"
                        label="Email address"
                        type="email"
                        required
                        className="secondRow2-cols-input"
                      />
                    </ScrollAnimation>
                  </Col>
                </Row>
                <Row>
                  <Col md={4} xs={12} className="secondRow2-cols">
                    <ScrollAnimation
                      animateIn="animate__fadeInLeft"
                      animateOut="animate__fadeOut"
                      duration={2}
                    >
                      <TextField
                        id="standard-basic"
                        label="Organization"
                        type="text"
                        required
                        className="secondRow2-cols-input"
                      />
                    </ScrollAnimation>
                  </Col>
                  <Col md={4} xs={12} className="secondRow2-cols">
                    <ScrollAnimation
                      animateIn="animate__fadeIn"
                      animateOut="animate__fadeOut"
                      duration={2}
                    >
                      <TextField
                        id="standard-basic"
                        label="Job Title"
                        type="text"
                        required
                        className="secondRow2-cols-input"
                      />
                    </ScrollAnimation>
                  </Col>
                  <Col md={4} xs={12} className="secondRow2-cols">
                    <ScrollAnimation
                      animateIn="animate__fadeInRight"
                      animateOut="animate__fadeOut"
                      duration={2}
                    >
                      <PhoneInput
                        international
                        defaultCountry="EG"
                        value={value}
                        onChange={setValue}
                        className="secondRow2-cols-input"
                        placeholder="Phone number"
                      />
                    </ScrollAnimation>
                  </Col>
                </Row>
                <Row>
                  <Col md={12} xs={12} className="secondRow2-cols">
                    <ScrollAnimation
                      animateIn="animate__fadeIn"
                      animateOut="animate__fadeOut"
                      duration={2}
                    >
                      <TextField
                        id="standard-basic"
                        label="How can we help?"
                        multiline
                        rows={3}
                        rowsMax={6}
                        required
                        className="secondRow2-cols-input"
                      />
                    </ScrollAnimation>
                  </Col>
                </Row>
                <Row>
                  <Col
                    md={12}
                    xs={12}
                    className="secondRow2-cols secondRow2-cols-submit-button"
                  >
                    <ScrollAnimation
                      animateIn="animate__fadeIn"
                      animateOut="animate__fadeOut"
                      duration={2}
                    >
                      <Button
                        variant="contained"
                        className="about-us-fp-button"
                      >
                        Submit
                      </Button>
                    </ScrollAnimation>
                  </Col>
                </Row>
              </form>
            </Row>
          </Col>
        </Row>
      </div>
    </Container>
  );
}

export default Contact;
